//Бургер

let burger = document.querySelector('.header__section .burger');
let burgerMenu = document.querySelector('.header__section .burger__menu');
let sectionContainer = document.querySelector('.section:nth-child(1)');
let header = document.querySelector('.header')

burger.addEventListener('click', function() {
  header.classList.toggle('_active')
  burger.classList.toggle('_active')
  burgerMenu.classList.toggle('_active')
  sectionContainer.classList.toggle('_active')
})
